// ÁîüÊàêÂºïÂØºÈóÆÈ¢ò
const generateFollowUpQuestion = (scenario, emotion) => {
  if (followUpQuestions[scenario]?.[emotion]) {
    const questions = followUpQuestions[scenario][emotion]
    const randomIndex = Math.floor(Math.random() * questions.length)
    return questions[randomIndex]
  }
  return null
}

// Ê£ÄÊü•ÊÉÖÊÑüÂÖ≥ÈîÆËØçÂæóÂàÜ
const check_emotion_keywords = (message) => {
  const emotionKeywords = {
    positive: ['ÂºÄÂøÉ', 'Âø´‰πê', 'È´òÂÖ¥', 'Ê£í', 'Â•Ω', 'ÂñúÊ¨¢', 'Áà±', 'ÊÑüË∞¢', 'ÊúüÂæÖ'],
    negative: ['ÈöæËøá', '‰º§ÂøÉ', 'ÁóõËã¶', 'ÁÉ¶ÊÅº', 'ËÆ®Âéå', 'ÁîüÊ∞î', 'ÁÑ¶Ëôë', 'ÂÆ≥ÊÄï'],
    neutral: ['ËøòË°å', '‰∏ÄËà¨', 'ÊôÆÈÄö', 'Ê≠£Â∏∏']
  }
  
  let score = 1
  const text = message.toLowerCase()
  
  // ÁßØÊûÅÊÉÖÊÑüÂä†ÂàÜ
  if (emotionKeywords.positive.some(word => text.includes(word))) {
    score += 0.5
  }
  // Ê∂àÊûÅÊÉÖÊÑü‰πüÈÄÇÂΩìÂä†ÂàÜÔºåÂõ†‰∏∫Ë°®Á§∫Áî®Êà∑ÂÄæËØâÊÑèÊÑøÂº∫
  if (emotionKeywords.negative.some(word => text.includes(word))) {
    score += 0.3
  }
  
  return score
}

// Ê£ÄÊü•ÊòØÂê¶ÂåπÈÖçÁî®Êà∑ÁîªÂÉè
const match_user_profile = (message) => {
  // ‰ªélocalStorageËé∑ÂèñÁî®Êà∑ÁâπÂæÅ
  const userTraits = personalityResult.value.traits || []
  const text = message.toLowerCase()
  
  // Ê£ÄÊü•Ê∂àÊÅØÊòØÂê¶ÂåÖÂê´Áî®Êà∑ÁâπÂæÅÁõ∏ÂÖ≥ÁöÑÂÖ≥ÈîÆËØç
  return userTraits.some(trait => {
    const traitKeyword = trait.replace(/[‚ú®üåüüéØüé®üí¨üé≠ü§îüòäüì±üéÆüìäü§ùüí™üåô‚òÄÔ∏è]/g, '').trim()
    return text.includes(traitKeyword)
  })
}

// ËÆ°ÁÆóÊ∂àÊÅØÊùÉÈáç
const calculate_weight = (message, position, shortTermLength) => {
  // Êó∂Èó¥Ë°∞ÂáèÔºöË∂äÊñ∞ÁöÑÊ∂àÊÅØÊùÉÈáçË∂äÈ´ò
  const time_decay = Math.pow(0.9, shortTermLength - position - 1)
  
  // ÊÉÖÊÑüÂàÜÊï∞
  const emotion_score = check_emotion_keywords(message)
  
  // ‰∏™ÊÄßÂåñÂàÜÊï∞ÔºöÂ¶ÇÊûúÂåπÈÖçÁî®Êà∑ÁîªÂÉèÂàôÊèêÂçáÊùÉÈáç
  const personalization = match_user_profile(message) ? 1.5 : 1
  
  return time_decay * (emotion_score + personalization)
}

// Âú®ÁîüÊàêÂõûÂ§çÊó∂‰ΩøÁî®ÊùÉÈáç
const generateResponse = (text, scenario) => {
  try {
    // Ê£ÄÊü•ÊòØÂê¶‰∏∫‰π±Á†ÅÊàñÊó†Â∫èÂ≠óÁ¨¶
    if (isGibberish(text)) {
      return 'ËØ∂ÂëÄÔΩûÊàëÁúã‰∏çÊáÇ‰Ω†ÂèëÁöÑÂÜÖÂÆπÂë¢ÔΩûÂèØ‰ª•Áî®Ê≠£Â∏∏ÁöÑÊñáÂ≠óË∑üÊàëËÅäÂ§©ÂêóÔºü‚ú®';
    }
    
    const analysis = analyzeEmotion(text);
    const { emotion, intensity, scenario: detectedScenario, keywords, isPositive } = analysis;
    
    // ÈÄâÊã©ÂõûÂ§çÁ≠ñÁï• - Ê≠£ÂêëÊÉÖÁª™‰ΩøÁî®ÂÖ±ÊÑüÔºåË¥üÂêëÊÉÖÁª™‰ΩøÁî®ÂÆâÊäö
    const strategy = isPositive ? 'empathize' : 'comfort';
    
    // ÊûÑÂª∫ÂõûÂ§ç
    let response = '';
    
    if (strategy === 'empathize') {
      // Ê≠£ÂêëÊÉÖÁª™Á≠ñÁï•ÔºöÂÖ±ÊÑü
      const empathyPhrases = [
        'ÊàëËÉΩÊÑüÂèóÂà∞‰Ω†ÁöÑ{{emotion}}ÔΩûÁúü‰∏∫‰Ω†ÂºÄÂøÉÔΩû‚ú®',
        '‰Ω†ÁöÑ{{emotion}}ÊÉÖÁª™ËÆ©Êàë‰πüÂèóÂà∞ÊÑüÊüìÔΩûÂ§™Ê£í‰∫ÜÔΩûüéâ',
        'ÁúüÂøÉ‰∏∫‰Ω†ÊÑüÂà∞{{emotion}}ÔΩûËøôÁúüÊòØ‰∏™Â•ΩÊ∂àÊÅØÔΩûüí´'
      ];
      
      // ÈÄâÊã©Áü≠ËØ≠Âπ∂ÊõøÊç¢ÊÉÖÁª™ËØç
      response = empathyPhrases[Math.floor(Math.random() * empathyPhrases.length)]
        .replace('{{emotion}}', emotionColorMap[emotion]?.label || 'ÊÑâÂø´');
      
    } else {
      // Ë¥üÂêëÊÉÖÁª™Á≠ñÁï•ÔºöÂÆâÊäö
      const comfortPhrases = [
        'Âê¨Âà∞‰Ω†ÊÑüÂà∞{{emotion}}ÔºåÊàëËÉΩÁêÜËß£ËøôÁßçÊÑüÂèóÔΩûüåß ÊÑøÊÑèÂíåÊàëÂ§öËØ¥ËØ¥ÂèëÁîü‰∫Ü‰ªÄ‰πàÂêóÔºü',
        'ÂΩì‰Ω†ÊÑüÂà∞{{emotion}}ÁöÑÊó∂ÂÄôÔºåÊÑøÊÑèÂíåÊàëËØ¥ËØ¥ÂèëÁîü‰∫Ü‰ªÄ‰πàÂêóÔΩûüçÉ Êàë‰ºöËÆ§ÁúüÂê¨‰Ω†ÂÄæËØâ„ÄÇ',
        'Èù¢ÂØπ{{emotion}}ÁöÑÊó∂ÂÄôÔºåÂÄæËØâÂá∫Êù•‰ºöÂ•ΩÂèó‰∏Ä‰∫õÔΩûüí≠ ‰Ω†ÊÉ≥ËÅäËÅäÂÖ∑‰ΩìÁöÑÂéüÂõ†ÂêóÔºü',
        'ÊúâÊó∂ÂÄôÊää{{emotion}}ËØ¥Âá∫Êù•‰ºöËΩªÊùæÂæàÂ§öÔºåÊàëÂú®ËøôÈáåÈô™ÁùÄ‰Ω†ÔΩû',
        'Â¶ÇÊûú‰Ω†ÊÑøÊÑèÔºåÂèØ‰ª•ËØ¶ÁªÜËØ¥ËØ¥‰Ω†ÁöÑÊÑüÂèóÔºåÊàë‰ºö‰∏ÄÁõ¥Èô™ÁùÄ‰Ω†ÔΩû'
      ];
      
      // ÈÄâÊã©Áü≠ËØ≠Âπ∂ÊõøÊç¢ÊÉÖÁª™ËØç
      response = comfortPhrases[Math.floor(Math.random() * comfortPhrases.length)]
        .replace('{{emotion}}', emotionColorMap[emotion]?.label || '‰∏çÈÄÇ');
    }
    
    return response;
    
  } catch (error) {
    console.error('ÁîüÊàêÂõûÂ§çÊó∂Âá∫Èîô:', error);
    return 'ËØ∂ÂëÄÔºå‰Ω†ËØ¥ÁöÑÂ§™ÈöæÂï¶ÔΩûËøôÊ¨°ÊàëÊ≤°ÊúâÂê¨ÊáÇÔΩû';
  }
};

// ÂèëÈÄÅÊ∂àÊÅØ
const sendMessage = async () => {
  if (!newMessage.value.trim() || isThinking.value) return;
  
  const messageContent = newMessage.value;
  
  // Ê£ÄÊü•Ê∂àÊÅØÈïøÂ∫¶
  if (messageContent.length > 500) {
    // Áî®Êà∑Ê∂àÊÅØËøáÈïø
    messages.value.push({
      type: 'assistant',
      content: '‰Ω†ÂèëÈÄÅÁöÑÊ∂àÊÅØÊúâÁÇπÈïøÂì¶ÔΩûËÉΩÁÆÄÁü≠‰∏Ä‰∫õÂêóÔºüÊàëÊúÄÂñúÊ¨¢ÁÆÄÁü≠ÂèàÊúâË∂£ÁöÑËÅäÂ§©Âï¶ÔΩû‚ú®'
    });
    return;
  }
  
  // Ê∑ªÂä†Áî®Êà∑Ê∂àÊÅØ
  messages.value.push({
    type: 'user',
    content: messageContent,
    timestamp: new Date().toISOString() // Ê∑ªÂä†Êó∂Èó¥Êà≥
  });
  
  // ‰øùÂ≠òÂà∞ÂéÜÂè≤
  saveUserMessageHistory(messageContent)
  
  // Á´ãÂç≥‰øùÂ≠òÂà∞localStorage
  localStorage.setItem('chatMessages', JSON.stringify(messages.value));
  
  // Ê∏ÖÁ©∫ËæìÂÖ•Ê°Ü
  newMessage.value = '';
  
  // ÁîüÊàêAIÂõûÂ§ç
  isThinking.value = true;
  try {
    // Ê®°ÊãüÂª∂Ëøü
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    // Ê†πÊçÆÁî®Êà∑Ê∂àÊÅØÂíåÂΩìÂâçÂú∫ÊôØÁîüÊàêÂõûÂ§ç
    const response = generateResponse(messageContent, selectedScenario.value);
    
    // Ê∑ªÂä†AIÂõûÂ§ç
    messages.value.push({
      type: 'assistant',
      content: response,
      timestamp: new Date().toISOString() // Ê∑ªÂä†Êó∂Èó¥Êà≥
    });
    
    // Ê∑ªÂä†Âà∞ÊÉÖÁª™Áì∂ - Âè™ÊúâÂΩì‰∏çÊòØ‰π±Á†ÅÊó∂
    if (!isGibberish(messageContent)) {
      addEmotionBottle(messageContent);
    }
    
    await scrollToBottom();
  } catch (error) {
    console.error('ÂèëÈÄÅÊ∂àÊÅØÂ§±Ë¥•:', error);
    messages.value.push({
      type: 'assistant',
      content: 'ËØ∂ÂëÄÔºå‰Ω†ËØ¥ÁöÑÂ§™ÈöæÂï¶ÔΩûËøôÊ¨°ÊàëÊ≤°ÊúâÂê¨ÊáÇÔΩû',
      timestamp: new Date().toISOString() // Ê∑ªÂä†Êó∂Èó¥Êà≥
    });
  } finally {
    isThinking.value = false;
  }
};

onMounted(() => {
  scrollToBottom()
  // ‰ªélocalStorageËé∑ÂèñÁî®Êà∑Á≠îÊ°à
  const answers = JSON.parse(localStorage.getItem('onboardingAnswers') || '{}')
  userAnswers.value = answers
  
  // Ê£ÄÊü•ÊòØÂê¶È¶ñÊ¨°ËøõÂÖ•
  const hasShownPanel = localStorage.getItem('hasShownPanel')
  console.log('Answers:', answers)
  console.log('Has shown panel:', hasShownPanel)
  
  // Â¶ÇÊûúÁî®Êà∑ÊúâÁ≠îÊ°à‰∏îÊú™ÊòæÁ§∫ËøáÈù¢ÊùøÔºåÂàôÊòæÁ§∫Èù¢Êùø
  if (Object.keys(answers).length > 0 && !hasShownPanel) {
    console.log('Showing panel')
    showPanel.value = true
    localStorage.setItem('hasShownPanel', 'true')
  }
  
  if (Object.keys(answers).length > 0) {
    const answerKey = Object.values(answers).join('')
    const result = personalityMap[answerKey]
    if (result) {
      personalityResult.value = {
        ...result,
        traits: generateTraits(answers)
      }
    }
  }
  
  // Á°Æ‰øùÊâÄÊúâÊ∂àÊÅØÈÉΩÊúâÊó∂Èó¥Êà≥
  messages.value.forEach(message => {
    if (!message.timestamp) {
      message.timestamp = new Date().toISOString();
    }
  });
  
  // ‰ªélocalStorageËé∑ÂèñÊÉÖÁª™Áì∂Êï∞ÊçÆ
  const savedEmotionBottles = localStorage.getItem('emotionBottles')
  if (savedEmotionBottles) {
    emotionBottles.value = JSON.parse(savedEmotionBottles)
  }
  
  // Âä†ËΩΩÂéÜÂè≤
  const savedUserHistory = localStorage.getItem('userMessageHistory')
  if (savedUserHistory) {
    userMessageHistory.value = JSON.parse(savedUserHistory)
  }
  
  // ‰ªélocalStorageËé∑ÂèñËÅäÂ§©ËÆ∞ÂΩï
  const savedMessages = localStorage.getItem('chatMessages');
  if (savedMessages) {
    messages.value = JSON.parse(savedMessages);
  }
})

