// Ê∂àÊÅØÊùÉÈáçËÆ°ÁÆóÁ≥ªÁªü
const calculateMessageWeight = (message, context) => {
  let weight = 1.0;
  
  // Êó∂Èó¥Ë°∞ÂáèÂõ†Â≠ê
  const timeDecay = Math.exp(-0.1 * (Date.now() - message.timestamp) / (1000 * 60 * 60)); // ÊØèÂ∞èÊó∂Ë°∞ÂáèÁ∫¶10%
  weight *= timeDecay;
  
  // ÊÉÖÊÑüÂàÜÊï∞
  const emotionAnalysis = analyzeEmotion(message.content);
  const emotionIntensity = emotionAnalysis.intensity || 1;
  weight *= (1 + 0.2 * emotionIntensity); // ÊÉÖÊÑüÂº∫Â∫¶ÊØèÂ¢ûÂä†1ÔºåÊùÉÈáçÂ¢ûÂä†20%
  
  // ‰∏™ÊÄßÂåñÂàÜÊï∞
  if (context.userTraits && context.userTraits.length > 0) {
    const matchingTraits = context.userTraits.filter(trait => 
      message.content.toLowerCase().includes(trait.toLowerCase())
    );
    weight *= (1 + 0.1 * matchingTraits.length); // ÊØèÂåπÈÖç‰∏Ä‰∏™ÁâπÂæÅÔºåÊùÉÈáçÂ¢ûÂä†10%
  }
  
  return weight;
};

