    { item: 'ÂëºÂê∏Áì∂', effect: 'Âπ≥ÈùôÂøÉÊÉÖ', probability: 0.7 },
    { item: 'Á¨ëËÑ∏Ê∞îÁêÉ', effect: 'ËΩ¨ÁßªÊ≥®ÊÑè', probability: 0.6 },
    { item: 'ÊüîËΩØÊä±Êûï', effect: 'ÂèëÊ≥ÑÊÉÖÁª™', probability: 0.8 }
  ],
